.text-input {
  border: 1px solid #cacccd;
  height: 42px;
  font-size: $font-size-large;
  font-weight: 300;
  padding: .8rem 1.2rem 1.2rem 3.2rem;
}

.text-input--join {
  @extend .text-input;
  margin-bottom: $m-size;
  padding: .8rem 1.2rem 0.8rem 3.2rem;
}

.select {
  @extend .text-input;
}

.textarea {
  @extend .text-input;
  height: 10rem;
}



