button,
input,
optgroup,
select,
textarea {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu,Cantarell,"Open Sans", "Helvetica Neue", sans-serif;
}

.header {
    background-color: rgba(83, 44, 165, 1) !important;
    z-index: 1;
}

.container {
    background-color: none;
}

