.container__left {
    background: none;
    border-radius: 0.3rem;
    display: table-cell;
    width: 25%;
}

.box-layout--join,
.box-layout--messages {
    // width: 100%;
    min-height: 1px;
    /*position: relative;*/
    min-height: 1px;
    display: table-cell;
    // width: 75%;
    vertical-align: middle;
}

.box-layout__box--join {
    margin-left: auto;
    margin-right: auto;
}

@media (max-width: 991px) {
    .container__left {
        width: 100%;
        display: block;
        height: auto;
    }

    .box-layout--join,
    .box-layout--messages {
        width: 100%;
        display: block;
        text-align: center;
    }

    .container {
        display: block
    }
}


.container__left {
    background-color: rgba(241, 243, 245, 1);
    padding-left: 0
}

.box-layout--join,
.box-layout--messages {
    background: none;
}

.row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.login-button {
    border-radius: 3px;
    background: #46325e;
    color: #fff;
    border: 1px solid #ccc;
}

.send-button {
    display:none;
}

.box-layout__box,
.box-layout__box--join {
    background-color: #f1f3f5;
}

.messages-box {
    position: static;
    background-color: transparent;
    margin-top: auto;
    max-width: 100vw;
}

@media (max-width: 991px) {
    .messages-box {
        max-height: 43vh
    }
}

.modal {
    display: block;
}

.room-name-wrapper {
    padding-left: 3.2rem;
}

@media (min-width: 991px) {
    .room-name-wrapper {
        height:7.4vh
    }
}

#message-form {
    /*position: static;
    width: auto*/
}

#message-form input {
    position: relative;
    flex: 1;
    flex-flow: row;
    -webkit-box-flex: 0;
    -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    max-width: 100%;
}

#message-form button {
    flex: 1;
    flex-flow: row;
    -webkit-box-flex: 0;
    -ms-flex: 0 0 25%;
    flex: 0 0 25%;
    max-width: 25%;
}


h1 {
    font-weight: 400;
    font-size: 2rem
}

header {
    width: 100%;
    position: fixed;
}

header+.container {
    padding-top: 66px
}

body, html, body > #app, body > #app > div, body > #app > div > div {
            height: 100%
}
.box-layout__box--join {
    margin: 6rem auto 6rem auto;
    border-radius: 3px;
    border: 1px solid #efefef;
}

.button--unread-messages + a {
    text-decoration:none;
}

.box-layout__title {
    margin: 0 0 1.6rem;
    line-height: 1;
    font-weight: 500;
    font-size: 2.5rem;
    text-shadow: 0px 0.11rem 1px #999;
}

/*.container__left {
	padding: 0 0 0 3.2rem;
}*/

.message-with-status {
    text-align: center;
}

/*.button--unread-messages {
  margin: auto;
}*/

.header__title .button--link {
    padding-right: 0
}

*:focus {
    outline: none;
}

.message-box {
    height: 50px;
}
.box-layout--messages {
    position: relative;
}
.room-header__title {
    color: white;
}
.photo_cont {
    width: 4rem;
    height: 4rem;
    border-radius: 100%;
    border: 1px solid #ccccd2;
    overflow: hidden;
    img {
        width: 3.8rem;
        height: 3.8rem;
    }
}
.rooms-joined-heading {
	padding-left: 3.2rem;
	padding-top: 1rem;
	font-weight: 700
}
.rooms-available-heading {
	padding-left: 3.2rem;
	font-weight: 700
}
@media (max-width: 991px) {
    .photo_cont {
        margin-left: auto;
        margin-right: auto;
    }
}
.message-with-status  {
	display:none
}
