#message-form {
  position: fixed;
  display: flex;
  width: inherit;
  bottom: 0;
  input {
    position: relative;
    // flex-grow: 0.5;
    flex: 1;
    flex-flow: row;
    border: none;
    border-top: 1px solid rgba(236, 236, 236, 1);
  }
  button {
    // flex: 1;
    flex-grow: 1;
  }
}

.messages-box {
  position: fixed;
  // float:right;
  // width: 100%;
  right: 0;
  //color: white;
  overflow-y: auto;
  word-wrap: break-word;
  padding-right: $l-size;
  height: 100%;
  max-height: 70vh;
  width: 100%;
  max-width: 80vw;
  margin-top: 10vh;
  
  // margin-left: 10%;  
  ul {
    list-style-type: none;
	padding-left: $l-size;
  }
}

.message {
  border-bottom: 1px solid fade-out(white, .8);
  line-height: 1rem;
}

.message__name {
  font-weight: 700;
  color: #3d3c3f;
  font-size: $font-size-large;
}

.message__time {
  font-style: italic;
  font-size: $font-size-small;
  color: #ccccd2;
}

.message__text {
  font-size: $font-size-small;
  line-height: 1.6rem;
  color: #09080b;
}

.message-with-status .message__text {
  color: #ccccd2;
}

.message-with-status {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(241, 243, 245, 1);
  color: #9e9ea6;
  font-style: italic;
  font-size: $font-size-large;
  /*padding: $s-size 0;*/ 
  margin: $s-size 0;
  border-radius: 3px;
}


