.button {
  background: $blue;
  border: none;
  color: white;
  display: inline-block;
  font-size: $font-size-large;
  font-weight: 300;
  line-height: 1;
  padding: $s-size;
  text-decoration: none;
}

.login-button {
  @extend .button;
  background: $github-color;
}

.button--leave-room {
  @extend .button;
  background: none;
  color: #FFF;
  //border: 1px solid #FFF;
  border-radius: 0;
  transition: background .3s linear, color .3s linear;
  &:hover {
    background: #ffff00;
    color: black;
    border-color: transparent;
  }
}

.button--join {
  @extend .button;
  background: #2ecc71;
  margin-left: $s-size;
}

.button--link {
  background: none;
}

.button--secondary {
  background: #888;
}


.button--unread-messages {
  border-radius:50%;
  height: 30px;
  width: 30px;
  min-width: 30px;
  max-width: 30px;
  min-height: 30px;
  max-height: 30px;
  background: #f1f3f5;
  color: #ccccd2;
  font-weight: 400;
  border: 1px solid #ccccd2;
}


